'use strict';

var _reqw = require('reqwest');
var Q = require('q');

function reqP(/* arguments */) {
  // wrap reqwest's non-compliant promise with a promise from Q that actually works
  return Q.when(_reqw.apply(null, arguments))
    .then(function(data) {
      if (data instanceof XMLHttpRequest) {
        data = JSON.parse(data.responseText);
      }

      return data;
    })
    .fail(function(err, message) {
      console.error(message, err);
    });
}

module.exports = {

  getAllCountries: function() {
    return reqP({ url: 'json/countries.json' });
  },

  getCountry: function(countryId) {
    return reqP({ url: 'json/countries/' + countryId + '.json' });
  },

  getProject: function(projectId) {
    return reqP({ url: 'json/projects/' + projectId + '.json' });
  }

};
