'use strict';

var Q = require('q');
var numeral = require('numeral');
var titleCase = require('to-title-case');
var L = window.L;  // explicitly bring into scope to not be evil (just for L.point, divIcon)

var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;

var DGSet = require('../util').DGSet;
var unique = require('../util').unique;
var arrayEq = require('../util').arrayEq;

var CountryActions = require('../actions/countryActions');
var MapViewActions = require('../actions/mapViewActions');
var MetaActions = require('../actions/pageMetaActions');
var FilterActions = require('../actions/filterActions');
var FiltersViewActions = require('../actions/filtersViewActions');

var SelectedCountryStore = require('../stores/selectedCountryStore');
var FilteredLocationsStore = require('../stores/filteredLocationsStore');
var ProjectsStore = require('../stores/projectsStore');
var BasemapsStore = require('../stores/basemapsStore');
var ThematicLayersStore = require('../stores/thematicLayersStore');

var EsriTileLayer = require('./map/esriTileLayer.jsx');
var EsriGisLayer = require('./map/esriGisLayer.jsx');
var ClusterLayer = require('./map/clusterLayer.jsx');
var Popup = require('./map/popup.jsx');
var locationPin = require('./locationPin');
var Filters = require('./filters.jsx');
var Header = require('./header.jsx');
var Legend = require('./legend.jsx');

var constants = require('../constants');


/**
 * Like arrayEq, but arguments don't have to be arrays.
 */
function laxArrayEq(a, b) {
  if (a !== b) {
    if (b === undefined) {
      return false;
    } else {
      return arrayEq(a, b);
    }
  } else {
    return true;
  }
}

var PopupItem = React.createClass({displayName: "PopupItem",
  render: function() {
    return (
      React.createElement("tr", {className: "total"}, 
        React.createElement("th", null, this.props.title), 
        React.createElement("td", null, 
          React.createElement("span", null, this.props.value)
        )
      )
    );
  },
});

var Country = React.createClass({displayName: "Country",

  mixins: [
    Reflux.connect(SelectedCountryStore, 'countries'),
    Reflux.connect(FilteredLocationsStore, 'locations'),
    Reflux.connect(BasemapsStore, 'basemaps'),
    Reflux.connect(ThematicLayersStore, 'thematicLayers'),
  ],

  getInitialState: function() {
    return {
      clustering: false,
      filtersWidth: 225,
    };
  },

  componentWillMount: function() {
    // make sure we load the country data. http cache should make this cheap.
    if (!(this.props.query.c instanceof Array)) {  // no countries? go home.
      console.error('Expected an array of country ids, got', this.props.query.c);
      MetaActions.transitionTo('international');
      return;
    }
    this.loadCountries(this.props.query.c);
    FilterActions.refreshFromURL();
    MapViewActions.refreshFromURL();
  },

  componentDidMount: function() {
    var map = this.props.getMap();
    map.on('focus', this.closeFilter);
  },

  componentWillUnmount: function() {
    var map = this.props.getMap();
    map.off('focus', this.closeFilter);
  },

  componentWillReceiveProps: function(nextProps) {
    // user maybe navigated from one country directly to another's url
    if (!(nextProps.query.c instanceof Array)) {
      console.error('Expected an array of country ids, got', nextProps.query.c);
      MetaActions.transitionTo('international');
      return;
    }
    if (!arrayEq(this.props.query.c, nextProps.query.c)) {
      this.loadCountries(nextProps.query.c);
    }
    if (!constants.categoricalFilterIds.every(function(id) {
        return laxArrayEq(this.props.query[id], nextProps.query[id]);
      }, this)) {
      FilterActions.refreshFromURL();
    }
    if (nextProps.query.bm !== this.props.query.bm || nextProps.query.thm !== this.props.query.thm) {
      MapViewActions.refreshFromURL();
    }
  },

  loadCountries: function(countryIds) {
    CountryActions.selectCountries(countryIds);
    var promises = countryIds.map(function(countryId) {
      return CountryActions.loadCountry.triggerPromise(countryId);
    });
    Q.allSettled(promises).then(this.setBounds);
  },

  setBounds: function(promises) {
    var allCoords = promises
      .filter(function(promise) { return (promise.state === 'fulfilled'); })
      .map(function(promise) { return promise.value; })
      .map(function(rawCountry) { return rawCountry.features; })
      .reduce(function(all, features) { return all.concat(features); })  // flatten nested array
      .map(function(location) {
        return location.geometry.coordinates.slice().reverse();  // TODO: ASDB-119
      });
    if (allCoords.length === 0) {
      console.error('no countries loaded? or...');
      return;
    }
    MapViewActions.changeBounds(L.latLngBounds(allCoords), {
      paddingTopLeft: [this.state.filtersWidth, 0],  // filters width and header height
    });
  },

  clusterIcon: function(cluster) {
    var childCount = cluster.getChildCount();

    var sectors = cluster.getAllChildMarkers()
      .map(function(location) {
        return location.feature.properties.sectors;
      })
      .reduce(function(a, b) { return a.concat(b); });
    sectors = DGSet(sectors);  // discard duplicates

    var iconContent;

    /* TODO: minor adjustments leaflet.SidePopup rightOffset and topOffset
        according to which of these is being used*/
    var dynamicPinSizeClass = 'hundreds';

    /*
    if (childCount < 9 && childCount > 0) {
      dynamicPinSizeClass = 'ones';

    } else if (childCount < 99 && childCount > 0){
      dynamicPinSizeClass = 'tens';
    }*/

    /* leaving dynamicPinSize here improves popup placement */
    var dynamicPinSize = new L.point(30, 30);
    var dynamicPinSizeClass = ' hundreds';

    if (childCount < 9 && childCount > 0) {
      dynamicPinSize = new L.point(23,23);
      dynamicPinSizeClass = ' ones';
    } else if (childCount < 99 && childCount > 0){
      dynamicPinSize = new L.point(27,27);
      dynamicPinSizeClass = ' tens';
    }

    if (sectors.length === 1) {
      var sector = sectors.asArray()[0];
      iconContent =
        '<span class="marker-image">' +
          '<img src="images/sector/' + sector + '.svg" alt="Sector: ' + sector + '" />' +
          '<span class="marker-image-number">' + childCount + '</span>' +
        '</span>';
    } else {
      iconContent = '<span class="marker-number">' + childCount + '</span>';
    }

    return L.divIcon({
      html: iconContent,
      className: ['marker-cluster', dynamicPinSizeClass].join(' '),
      iconSize: dynamicPinSize, /* Overridden by markers.scss */
    });
  },

  highlightProject: function(id) {
    var cluster = this.refs.clusterLayer.layer;
    cluster.getLayers()
      .filter(function(layer) {
        return (layer.feature.properties.id === id);
      })
      .map(function(layer) {
        return cluster.getVisibleParent(layer);
      })
      .forEach(this.refs.clusterLayer._addPopupOpenClass);
  },

  clearHighlight: function() {
    if (this.refs.clusterLayer !== undefined) {
      var cluster = this.refs.clusterLayer.layer;
      cluster.getLayers()
        .map(function(layer) {
          return cluster.getVisibleParent(layer);
        })
        .forEach(this.refs.clusterLayer._rmPopupOpenClass);
    }
  },

  locationPopup: function(location) {
    var project = ProjectsStore.getById(location.properties.id);
    var sectors = location.properties.sectors.reduce(function(prev, curr) {
      return prev + ', ' + curr;
    });
    var formatter = '$0,0';
    this.highlightProject(project.id);
    return (
      React.createElement(Popup, {className: "general-popup"}, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, location.properties.locationName, ", ", location.properties.countryName)
        ), 
        React.createElement("div", {className: "panel-body totals project-details"}, 
          React.createElement("h4", null, titleCase(location.properties.title)), 
          React.createElement("p", {className: "country-summary"}, " ", location.properties.info, " "), 
          React.createElement("table", {className: "table table-condensed"}, 
            React.createElement("tbody", null, 
              React.createElement(PopupItem, {title: "Project ID", value: location.properties.id}), 
              React.createElement(PopupItem, {title: "Sector(s)", value: sectors}), 
              React.createElement(PopupItem, {title: "Approval Date", value: location.properties.approvalDate || 'unknown'}), 
              React.createElement(PopupItem, {title: "Commitment Amount", value: numeral(location.properties.commitmentAmount).format(formatter)}), 
              React.createElement(PopupItem, {title: "Disbursement Amount", value: numeral(location.properties.disbursementAmount).format(formatter)}), 
              React.createElement(PopupItem, {title: "Closing Date", value: location.properties.closingDate || 'unknown'})
            )
          ), 
          React.createElement("p", {className: "details"}, 
            React.createElement("span", null, /* need this span wrapper for flexbox! */
              React.createElement("span", {className: "locations-count"}, project.locations.length - 1), " other locations.", ' '
            ), 
            React.createElement(Link, {to: "project", params: {projectId: location.properties.id, countryId: location.properties.countryId}}, 
              "Visit project page"
            )
          )
        )
      )
    );
  },

  clusterPopup: function(clusterData) {
    var locations = clusterData.features
      .map(function(feature) { return feature.properties; });

    var projects = unique(locations.map(function(l) { return l.id; }))
      .map(ProjectsStore.getById);

    return (
      React.createElement(Popup, null, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, 
            projects.length, " projects with ", locations.length, " locations here"
          )
        ), 
        React.createElement("div", {className: "panel-body"}, 
          React.createElement("ul", {className: "project-list"}, 
            projects.map(function(project) {
              return (
                React.createElement("li", {key: project.id}, 
                  React.createElement(Link, {
                    to: "project", 
                    params: {projectId: project.id, countryId: project.countryId}, 
                    onMouseOver: this.highlightProject.bind(this, project.id), 
                    onMouseOut: this.clearHighlight}, 
                    React.createElement("span", {className: "bullet-imgs"}, 
                      project.sectors.map(function(sector) {
                        return React.createElement("img", {
                          key: sector, 
                          className: "project-sector-icon", 
                          src: 'images/sector/' + sector + '.svg', 
                          alt: 'Sector: ' + sector})
                      })
                    ), 
                    titleCase(project.title), " (", project.locations.length, " locations)"
                  )
                )
              );
            }, this)
          ), 
          React.createElement("a", {className: "btn btn-link", onClick: this.zoomToCluster(clusterData)}, 
            React.createElement("span", {className: "cluster-count"}, React.createElement("span", {className: "glyphicon glyphicon-zoom-in"})), " Zoom to cluster"
          )
        )
      )
    );
  },

  // riseOnHover the hard way, not thanks to leaflet.markerCluster
  clusterMouseover: function(e) {
    var el = e.layer._icon;
    el.style.zIndex = 10000;
  },

  clusterMouseout: function(e) {
    var el = e.layer._icon;
    el.style.zIndex = e.layer._zIndex;
  },

  locationDblclick: function(feature, layer) {  // on a non-cluster location
    MetaActions.transitionTo('project', {
      countryId: feature.properties.countryId,
      projectId: feature.properties.id,
    });
  },

  clusterDblclick: function(what) {
    what.layer.zoomToBounds();
  },

  zoomToCluster: function(clusterData) {
    // the outer function is just to close over `clusterData`
    return function() {
      MapViewActions.changeBounds(clusterData.event.layer.getBounds(), {maxZoom: 12});
    };
  },

  filtersWidthChange: function(newWidth) {
    this.setState({filtersWidth: newWidth});
  },

  getPopupPadding: function() {
    return {
      topLeft: [this.state.filtersWidth + 20, 0],
    };
  },

  clusterProgress: function(processed, total, elapsed) {
    var progress = processed / total;
    if (progress === 1) {
      this.setState({clustering: false});
    } else {
      this.setState({clustering: progress});
    }
  },


  getThematicLayer: function() {
    var thematicObj = this.state.thematicLayers.filter(function(thematic) {
      return thematic.selected;
    })[0];
    if (thematicObj !== undefined) {
      return thematicObj;
    } else {
      return null;
    }
  },


  getBasemap: function() {
    var basemapObj = this.state.basemaps.filter(function(basemap) {
      return basemap.selected;
    })[0];
    if (basemapObj !== undefined) {
      return basemapObj.id;
    } else {
      console.error('Could not find selected basemap to show, defaulting to streets.');
      return 'Streets';
    }
  },

  maybeCloseFilter: function(e) {
    if (e.keyCode === 27) {  // ESC
      this.closeFilter();
    }
  },

  closeFilter: function() {
    FiltersViewActions.close();
  },

  render: function() {
    var countryNames = this.state.countries.features
      .map(function(country) { return country.properties.name; });
    if (countryNames.length > 3) {
      countryNames = countryNames.slice(0, 3);
      countryNames.push(' + ' + (this.state.countries.features.length - 3) + ' more')
    }
    if (this.state.countries.features.length < this.props.query.c.length) {
      countryNames.push('loading...');
    }
    if (this.state.clustering) {
      countryNames.push('clustering ' + numeral(this.state.clustering).format('0%'));
    }
    var locationsCount = this.state.locations.length;

    var header =(
      React.createElement(Header, null, 
        React.createElement(Header.Main, null, 
          React.createElement(Header.Item, {className: "hidden-xs"}, 
            React.createElement(Link, {to: "international", className: "back"}, 
              "Global view"
            )
          ), 
          React.createElement(Header.Item, null, 
            React.createElement("a", {className: "country"}, 
              countryNames.join(', ') + ' – ' + locationsCount + ' Project Locations'
            )
          )
        )
      )
    );

    return (
      React.createElement("div", {onKeyUp: this.maybeCloseFilter}, 
        React.createElement(EsriTileLayer, {theme: this.getBasemap(), getMap: this.props.getMap}), 
        React.createElement(EsriGisLayer, {thematicLayers: this.getThematicLayer(), getMap: this.props.getMap}), 
        header, 
        React.createElement(Legend, {thematicLayers: this.getThematicLayer()}), 
        React.createElement(ClusterLayer, {
          ref: "clusterLayer", 
          getMap: this.props.getMap, 
          layerData: this.state.locations, 
          showCoverageOnHover: false, 
          zoomToBoundsOnClick: false, 
          spiderifyOnMaxZoom: false, 
          maxClusterRadius: 15, 
          iconCreateFunction: this.clusterIcon, 
          chunkedLoading: true, 
          chunkProgress: this.clusterProgress, 
          pin: locationPin, 
          dblclick: this.locationDblclick, 
          clusterDblclick: this.clusterDblclick, 
          popup: this.locationPopup, 
          popupClose: this.clearHighlight, 
          clusterPopupFunction: this.clusterPopup, 
          clusterMouseoverFunction: this.clusterMouseover, 
          clusterMouseoutFunction: this.clusterMouseout, 
          getPopupPadding: this.getPopupPadding}), 
        React.createElement(Filters, {
          notifyWidthChange: this.filtersWidthChange, 
          query: this.props.query})
      )
    );
  }

});


module.exports = Country;
